.products {
  &__products-section {
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 64px;

      &:last-child {
        margin-bottom: 40px;
      }
    }
  }

  &__image-container {
    position: relative;

    box-sizing: border-box;
    max-height: 321px;
    width: 100%;
    overflow: hidden;

    margin-bottom: 16px;

    @include onTablet {
      height: 187px;
      padding-bottom: 0;
    }

    @include onDesktop {
      height: 321px;
      padding-bottom: 0;
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

  }

  &__image {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: right;
    transition: transform 1s;

    &--first {
      object-position: center;
    }

    &:hover {
      transform: scale(1.5);
    }
  }

  &__description {
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;

    color: $text-color;

    // margin-bottom: 40px;

    @include onTablet {
      font-size: 24px;

      // margin-bottom: 64px;
    }
  }
}
